
import React, { useState } from "react";
import axios from 'axios'
import { useNavigate } from "react-router-dom"

function Signup() {
    const [name, setName] = useState()
    const [email, setEmail] = useState()
    const [password, setPassword] = useState()
    const [error, setError] = useState('');
    const navigate = useNavigate()

    const handleSubmit = (e) => {
        e.preventDefault();
        axios.post('http://localhost:3001/register', { name, email, password })
            .then(result => {
                console.log(result)
                navigate('/login')
            })
            .catch(err => {
                console.log(err);
                if (err.response && err.response.data.message) {
                    setError(err.response.data.message);  // Display the error message
                }
            })

    }

    return (
        <>
            <div className="login-form mt-5 pt-2">
                <div className="App-header">
                    <div className="card-1" style={{ width: '400px' }}>
                        <img
                            src="/img/Header-Logo-Original.png"
                            alt="Logo"
                            style={{ width: '180px' }}
                            className="card-img-top mx-auto mt-4"
                        />
                        <form onSubmit={handleSubmit}>
                            <div className="card-body">
                                {/* Username or Email Input */}
                                <input
                                    type="text"
                                    className="form-control mt-3"
                                    placeholder="Username"

                                    onChange={(e) => setName(e.target.value)}
                                />
                                <input
                                    type="email"
                                    className="form-control mt-4"
                                    placeholder="Email"
                                    onChange={(e) => setEmail(e.target.value)}

                                />


                                {/* Password Input */}
                                <input
                                    type="password"
                                    className="form-control mt-4"
                                    placeholder="Password"
                                    onChange={(e) => setPassword(e.target.value)}
                                />
                           {error && <p className="mt-4 fs-2" style={{color: '#ed4444'}}>{error}</p>} 

                                {/* Login Button */}
                                <div className="d-grid gap-2 mt-5">
                                    <button
                                        type="submit"
                                        className="btn btn-primary"

                                    >
                                        Sign Up
                                    </button>
                                </div>
                            </div>
                        </form>





                    </div>

                    <div className="card-1 mt-4" style={{ width: '20rem' }}>
                        <p className="mt-2 mb-3 text-dark">
                            Don't have an account? <a href="/login">Login</a>
                        </p>
                    </div>

                    <p className="mt-3">Get the app.</p>
                    <img src="/img/logo-b.PNG" alt="App Logo" />
                </div>
            </div>
        </>
    );

};
export default Signup;