import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';

const AccountPage = () => {
  const [userDetails, setUserDetails] = useState(null);
  const [orders, setOrders] = useState([]);
  const [error, setError] = useState(null);
  const navigate = useNavigate();

  useEffect(() => {
    const token = localStorage.getItem('token');

    if (!token) {
      navigate('/login');
      return;
    }

    fetch('http://localhost:3001/user/details', {
      method: 'GET',
      headers: { Authorization: `Bearer ${token}` },
    })
      .then((response) => response.json())
      .then((data) => {
        if (data.user) {
          setUserDetails(data.user);
          fetchOrders();
        } else {
          setError('User details not found');
        }
      })
      .catch((err) => {
        console.error('Error fetching user details:', err);
        setError('Failed to fetch user details');
      });
  }, [navigate]);

  function fetchOrders() {
    fetch('http://localhost:3001/user/orders', {
      method: 'GET',
      headers: { Authorization: `Bearer ${localStorage.getItem('token')}` },
    })
      .then((response) => response.json())
      .then((data) => {
        setOrders(data.orders || []);
      })
      .catch((err) => {
        console.error('Error fetching orders:', err);
        setError('Failed to fetch orders');
      });
  }

  // Logout Function
  const handleLogout = () => {
    localStorage.removeItem('token'); // Clear token from storage
    navigate('/login'); // Redirect to login page
  };

  if (error) return <div>{error}</div>;
  if (!userDetails) return <div>Loading...</div>;

  return (
    <div className='container mt-5'>
      <div className="d-flex justify-content-between align-items-center">
        <h2>My Account</h2>
        <button className="btn btn-danger" onClick={handleLogout}>Logout</button>
      </div>

      <div className="d-flex align-items-start mt-3">
        <div className="nav flex-column nav-pills mr-3" id="v-pills-tab" role="tablist" aria-orientation="vertical">
          <a className="nav-link active" id="v-pills-profile-tab" data-toggle="pill" href="#v-pills-profile" role="tab" aria-controls="v-pills-profile" aria-selected="false">Profile</a>
          <a className="nav-link" id="v-pills-messages-tab" data-toggle="pill" href="#v-pills-messages" role="tab" aria-controls="v-pills-messages" aria-selected="false">Order</a>
          <a className="nav-link" id="v-pills-settings-tab" data-toggle="pill" href="#v-pills-settings" role="tab" aria-controls="v-pills-settings" aria-selected="false">Settings</a>
          <a className="nav-link" id="v-pills-setting-tab" data-toggle="pill" href="#v-pills-setting" role="tab" aria-controls="v-pills-setting" aria-selected="false">Address</a>
        </div>

        <div className="tab-content w-100" id="v-pills-tabContent">
          <div className="tab-pane fade show active" id="v-pills-profile" role="tabpanel" aria-labelledby="v-pills-profile-tab">
            <div className='ml-5'>
              <h5>Account Details</h5>
              <p><strong>Name:</strong> {userDetails.name}</p>
              <p><strong>Email:</strong> {userDetails.email}</p>
            </div>
          </div>

          <div className="tab-pane fade w-100" id="v-pills-messages" role="tabpanel" aria-labelledby="v-pills-messages-tab">
            <h5>Order History</h5>
            {orders.length === 0 ? (
              <p>No orders found.</p>
            ) : (
              <ul >
                {orders.map((order) => (
                  <div key={order._id}>
                    <p className='mt-5 pt-2'><strong>Order ID:</strong> {order._id}</p>
                    <p> {order.createdAt.slice(0, 10)} - {order.createdAt.slice(11, 16)} </p>
                    <p><strong>Status:</strong> {order.status}</p>
                    <p><strong>Total Price:</strong> ₹ {order.cartItems.reduce((sum, item) => sum + item.price * item.quantity, 0)}</p>
                    <div className='d-flex flex-wrap'>
                      {order.cartItems.map((item, index) => (
                        <div className='ordercart-box' key={index}>
                          <div className='d-flex mt-4'>
                            <div><img className='cart-img' src={item.img} alt={item.name} /></div>
                            <div className='w-100'>
                              <h6 className='d-flex mb-0'>
                                <span>{item.name}</span>
                              </h6>
                              <p><strong>Quantity:</strong> {item.quantity}</p>
                              <h6 className='mb-0'><strong>Total Price:</strong> ₹ {item.price} * {item.quantity} = ₹ {item.price * item.quantity}</h6>
                            </div>
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>
                ))}
              </ul>
            )}
          </div>

          <div className="tab-pane fade" id="v-pills-settings" role="tabpanel" aria-labelledby="v-pills-settings-tab">Settings Content</div>
          <div className="tab-pane fade" id="v-pills-setting" role="tabpanel" aria-labelledby="v-pills-setting-tab">
            <div className='ml-5'>
              <h5>Account Details</h5>
              <p><strong>Name:</strong> {userDetails.name}</p>
              <p><strong>Email:</strong> {userDetails.email}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AccountPage;
