import React, { useState, useContext } from 'react';
import { CartContext } from '../CartContext'; 

const Navbar = () => {
  const [isOpen, setIsOpen] = useState(false);
  const { cart, removeFromCart, updateQuantity } = useContext(CartContext); 

  const toggleOffcanvas = () => {
    setIsOpen((prevState) => !prevState);
  };

  const closeOffcanvas = () => {
    setIsOpen(false);
  };


  return (
    <>
      <div className="container-fluid">
        <div className="row align-items-center py-3 px-xl-5">
          <div className="col-lg-3 d-none d-lg-block">
            <a href="" className="text-decoration-none">
              <img src="/img/Header-Logo-Original.png" alt="Logo" />
            </a>
          </div>
          <div className="col-lg-6 col-6 text-left">
            <form action="">
              <div className="input-group">
                <input type="text" className="form-control" placeholder="Search for products" />
                <div className="input-group-append">
                  <span className="input-group-text bg-transparent text-green">
                    <i className="fa fa-search"></i>
                  </span>
                </div>
              </div>
            </form>
          </div>
          <div className="col-lg-3 col-6 text-right">
            <a href="" className="btn border">
              <i className="fas fa-heart text-green"></i>
              <span className="badge">0</span>
            </a>
            <button onClick={toggleOffcanvas} className="btn border">
              <i className="fas fa-shopping-cart text-green"></i>
              <span className="badge">{cart.length}</span>
            </button>

          </div>
        </div>
      </div>

      <div className="container-fluid bg-gr-dark">
        <div className="row border-top px-xl-5">
          <div className="col-lg-3 navbar navbar-expand-lg p-0 d-none d-lg-block">
            <div
              className="d-flex align-items-center justify-content-between text-white w-100"
              style={{ height: '65px', margintop: '-1px', padding: '0 30px ' }}
            >
              <div className="nav-item dropdown">
                <a
                  href="#"
                  className="nav-link dropdown-toggle bg-yellow text-white"
                  id="navbarDropdown1"
                  role="button"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  ALL CATEGORIES
                </a>
                <div className="dropdown-menu" aria-labelledby="navbarDropdown1">
                  <a href="product.html" className="dropdown-item nav-link">Product</a>
                  <a href="combo.html" className="dropdown-item nav-link">Combo</a>
                  <a href="combo-pack.html" className="dropdown-item nav-link">Combo-pack</a>
                </div>
              </div>
            </div>
          </div>
          <div className="col-lg-9">
            <nav className="navbar navbar-expand-lg bg-light navbar-light py-3 py-lg-0 px-0">
              <a href="" className="text-decoration-none d-block d-lg-none">
                <img className="h-50" src="/img/Footer-Logo-Original.png" alt="Logo" />
              </a>
              <button type="button" className="navbar-toggler" data-toggle="collapse" data-target="#navbarCollapse">
                <span className="navbar-toggler-icon"></span>
              </button>
              <div className="collapse navbar-collapse justify-content-between" id="navbarCollapse">
                <div className="navbar-nav mr-auto py-0">
                  <a href="/" className="nav-item nav-link active">Home</a>
                  <a href="/about" className="nav-item nav-link">About Us</a>
                  <a href="/product" className="nav-item nav-link">product</a>
                  <a href="#" className="nav-item nav-link">Track Order</a>
                  <a href="contact.html" className="nav-item nav-link">Contact Us</a>
                </div>
                <div className="navbar-nav ml-auto py-0">
                  <div
                    className="d-flex align-items-center justify-content-between text-white w-100"
                    style={{ height: '65px', marginTop: '-1px', padding: '0 30px' }}
                  >
                    <div className="nav-item dropdown">
                      <a
                        href="#"
                        className="nav-link dropdown-toggle bg-gr-light text-light"
                        id="navbarDropdown2"
                        role="button"
                        data-toggle="dropdown"
                        aria-haspopup="true"
                        aria-expanded="false"
                      >
                        Account
                      </a>
                      <div className="dropdown-menu" aria-labelledby="navbarDropdown2">
                      <a href="/account" className="dropdown-item">Account</a>
                        <a href="/login" className="dropdown-item">Login</a>
                        <a href="/sign-up" className="dropdown-item">Sign in</a>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </nav>
          </div>
        </div>
      </div>



      {/* Offcanvas Cart */}
      {isOpen && (
        <div className="offcanvas-backdrop" onClick={closeOffcanvas}></div>
      )}
      <div className={`offcanvas ${isOpen ? 'open' : ''}`}>
        <div className="offcanvas-header d-flex justify-content-between">
          <h5>Cart</h5>
          <button onClick={closeOffcanvas}>
            <i className="fas fa-times"></i>
          </button>
        </div>
        <div className="offcanvas-body">
          <h5>Shopping Cart</h5>
          <div>
            {cart.map(item => (
              <>
              <div key={cart.id} className='d-flex mt-4'>
              <div ><img className='cart-img' src={item.img}></img></div>
              <div className='w-100'>
                <h6 className='d-flex mb-0'>
              <span>{item.name} </span>
              <span className='ml-auto'>  <button className='btn p-0' onClick={() => removeFromCart(item.id)}> <i className="fas fa-times"></i></button></span>
              </h6>
             
             <div className='d-flex '>
              <h6 className='mb-0 '>${item.price}</h6>
              <h6 className='mb-0 ml-auto mr-auto' key={item.id}>
              
             
             
               <span><button className='btn pt-0' onClick={() => updateQuantity(item.id, item.quantity - 1)} disabled={item.quantity <= 1}>-</button></span>
               {item.quantity}
               <span> <button className='btn pt-0' onClick={() => updateQuantity(item.id, item.quantity + 1)}>+</button></span>
              
             </h6>
             </div>
             
              </div>
              </div>
              </>
            ))}
          </div>
          <h5 className='mt-5 mb-5'>Total: ${cart.reduce((total, item) => total + item.price * item.quantity, 0)}</h5>
          <a href='/checkout'>Check out</a>
        </div>
      </div>
    </>
  );
};

export default Navbar;