import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";

const Login = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const navigate = useNavigate();

  const handleSumbit = (e) => {
    e.preventDefault();
    axios.post('http://localhost:3001/login', { email, password })
      .then(result => {
        if (result.data.message === "Success") {
          localStorage.setItem('token', result.data.token);
          navigate('/account');  // Redirect to account page
        }
      })
      .catch(err => console.error('Login error:', err));
  };

  return (
    <>
      <div className="login-form mt-5">
        <div className="App-header">
          <form onSubmit={handleSumbit}>
            <div className="card-1" style={{ width: '390px' }}>
              <img
                src="/img/Header-Logo-Original.png"
                alt="Logo"
                style={{ width: '180px' }}
                className="card-img-top mx-auto mt-3"
              />
              <div className="card-body">
                <input
                  type="email"
                  className="form-control"
                  placeholder="Phone number, username or email"
                  onChange={(e) => setEmail(e.target.value)}
                />
                <input
                  type="password"
                  className="form-control mt-3"
                  placeholder="Password"
                  onChange={(e) => setPassword(e.target.value)}
                />
                <div className="d-grid gap-2 mt-4">
                  <button type="submit" className="btn btn-primary">
                    Login
                  </button>
                </div>
              </div>
              <a> <p className="text-dark">Forgot password?</p></a>
            </div>
          </form>
          <div className="card-1 mt-4" style={{ width: '20rem' }}>
            <p className="mt-2 mb-3 text-dark">
              Don't have an account? <a href="/sign-up">Sign up</a>
            </p>
          </div>
          <p className="mt-3">Get the app.</p>
          <img src="/img/logo-b.PNG" alt="App Logo" />
        </div>
      </div>
    </>
  );
};

export default Login;
