import React, { useContext, useState } from 'react';
import { CartContext } from '../CartContext'; // Import CartContext

const ShoppingCart = () => {
  const { cart, addToCart,removeFromCart, updateQuantity  } = useContext(CartContext); 

  const [category, setCategory] = useState("All");
  const [priceRange, setPriceRange] = useState([50, 1000]);


  const products = [
    { id: 1, name: "Coconut Oil", category: "oil", price: 77, quantity: 1, img: "/img/home/PRODUCT-21.png" },
    { id: 2, name: "GINGELLY OIL", category: "oil", price: 50, quantity: 1, img: "/img/home/PRODUCT-18.png" },
    { id: 3, name: "GROUNDNUTOIL", category: "oil", price: 500, quantity: 1, img: "/img/home/PRODUCT-21.png" },
    { id: 4, name: "FENUGREEK", category: "grams", price: 500, quantity: 1, img: "/img/home/PRODUCT-1.png" },
    { id: 5, name: "TOOR DHALL", category: "dhall", price: 600, quantity: 1, img: "/img/home/PRODUCT-2.png" },
    { id: 6, name: "ORID DHALL", category: "dhall", price: 700, quantity: 1, img: "/img/home/PRODUCT-11.png" },
    { id: 7, name: "FRIED GRAM", category: "grams", price: 590, quantity: 1, img: "/img/home/PRODUCT-21.png" },
    { id: 8, name: "JAGGERY POWDER", category: "sweetner", price: 800, quantity: 1, img: "/img/home/PRODUCT-15.png" },
    { id: 9, name: "FENNEL SEEDS", category: "grams", price: 400, quantity: 1, img: "/img/home/PRODUCT-9.png" },
    { id: 10, name: "BLACK PEPPER", category: "grams", price: 300, quantity: 1, img: "/img/home/PRODUCT-6.png" },
  ];



  const filteredProducts = products.filter(product => {
    return (
      (category === "All" || product.category === category) &&
      product.price >= priceRange[0] &&
      product.price <= priceRange[1]
    );
  });



  return (

    <>



      <div className="container">
        <div className="row d-flex">
          <div className="col-lg-9 col-sm-12 ">
          <div className="text-left mr-auto mt-5">
                        <h2 className="text-light  top-sell p-2 bg-gr-light object-fit">Product Filter</h2>
                    </div>
          
          <div>
              <select className='btn' onChange={(e) => setCategory(e.target.value)}>
                <option value="All">All Categories</option>
                <option value="oil">Oils</option>
                <option value="dhall">Dhall</option>
                <option value="sweetner">Sweetner</option>
                <option value="grams">Grams</option>
              </select>

              <input
                type="range"
                min="50"
                max="1000"
                value={priceRange[1]}
                onChange={(e) => setPriceRange([50, e.target.value])}
                className='btn ml-5'
              />
              <span>{`Price: $${priceRange[0]} - $${priceRange[1]}`}</span>
              </div>
            <div className="row justify-content-center">
              

              {filteredProducts.map((product) => (
                <div key={product.id} className="d-flex justify-content-center mt-3">
                  <div className="card">
                    <img className="w-100" src={product.img} alt={product.name} />
                    <h5>{product.name}</h5>
                    <p>${product.price}</p>
                    <button className='product-add-btn' onClick={() => addToCart(product)}>Add to Cart</button>
                  </div>
                </div>
              ))}
            </div>
          </div>
          <div className="col-lg-3 col-sm-12">
          <h5 className='mt-5'>Shopping Cart</h5>
          <div>
            {cart.map(item => (
              <>
              <div className='d-flex mt-4'>
              <div ><img className='cart-img' src={item.img}></img></div>
              <div className='w-100'>
                <h6 className='d-flex mb-0'>
              <span>{item.name} </span>
              <span className='ml-auto'>  <button className='btn p-0' onClick={() => removeFromCart(item.id)}> <i className="fas fa-times"></i></button></span>
              </h6>
             
             <div className='d-flex '>
              <h6 className='mb-0 '>${item.price}</h6>
              <h6 className='mb-0 ml-auto mr-auto' key={item.id}>
              
             
             
               <span><button className='btn pt-0' onClick={() => updateQuantity(item.id, item.quantity - 1)} disabled={item.quantity <= 1}>-</button></span>
               {item.quantity}
               <span> <button className='btn pt-0' onClick={() => updateQuantity(item.id, item.quantity + 1)}>+</button></span>
              
             </h6>
             </div>
             
              </div>
              </div>
              </>
            ))}
          </div>
        
            <button className='btn' ><a href='/Checkout'>checkout</a></button>
          </div>
        </div>
      </div>
    </>
  );
};

export default ShoppingCart;
