import React, { useRef } from 'react';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';

const Home = () => {
    const settings = {
        dots: false,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true,
    };
    const settingsproduct = {
        dots: false,
        infinite: true,
        speed: 500,
        slidesToShow: 4,
        slidesToScroll: 2,
        autoplay: false,
        loop: true,
    };

    // Create reference for the product slider
    const productSliderRef = useRef(null);

    // Function to move the carousel to the next slide
    const goToNextSlide = () => {
        if (productSliderRef.current) {
            productSliderRef.current.slickNext();
        }
    };

    // Function to move the carousel to the previous slide
    const goToPrevSlide = () => {
        if (productSliderRef.current) {
            productSliderRef.current.slickPrev();
        }
    };

    return (
        <>
            <div className='container-fluid w-100 p-0 m-0'>
                <Slider {...settings} className='h-100 p-0 m-0'>
                    <div className='p-0 w-100'>
                        <img className='w-100' src="/img/home/Nalam-landing-banner.jpg" alt="Banner 1" />
                    </div>
                    <div className='p-0'>
                        <img className='w-100' src="/img/home/banner.jpeg" alt="Banner 2" />
                    </div>
                </Slider>
            </div>


            <div className="container spices-box">
                <div className="row align-items-center justify-content-center br-gr px-xl-5 pb-3">


                    <div className="align-items-center justify-content-center   feature-box mb-4" style={{padding: "25px"}}>
                        <img className="product-hr p-2" src="/img/home/icon-1.png" />
                        <h5 className=" text-center text-light  mt-2">Spices</h5>
                    </div>


                    <div className=" align-items-center justify-content-center feature-box mb-4" style={{padding: "25px"}}>
                        <img className="product-hr p-2 text-center" src="/img/home/icon-2.png" />
                        <h5 className="m-0 text-center text-light mt-2">Pulses</h5>
                    </div>

                    <div className=" align-items-center justify-content-center feature-box mb-2" style={{padding: "25px"}}>
                        <img className="product-hr p-2" src="/img/home/icon-3.png" />
                        <h5 className=" m-0 text-center text-light  mt-2">Natural <br />Sweetners</h5>
                    </div>

                    <div className=" align-items-center justify-content-center feature-box  mb-4" style={{padding: "25px"}}>
                        <img className="product-hr p-2" src="/img/home/icon-4.png" />
                        <h5 className=" m-0 text-center text-light  mt-2">Oil</h5>
                    </div>

                    <div className="align-items-center justify-content-center feature-box mb-4" style={{padding: "25px"}}>
                        <img className="product-hr p-2" src="/img/home/icon-5.png" />
                        <h5 className=" m-0 text-center text-light  mt-2">Combos's</h5>
                    </div>


                </div>
            </div>

            <div className="container-fluid p-0 pt-5">
                <img className="w-100" src="/img/home/banner-11.png" />
            </div>


            <div className="container pt-5 pb-5">
                <div className="d-flex align-items-center justify-content-between mb-4">
                    <div className="text-left">
                        <h2 className="text-light text-center top-sell p-2 bg-gr-light">NEW ARRIVALS</h2>
                    </div>
                    <div className=" justify-content-end ms-auto">

                        <button onClick={goToPrevSlide} className="carousel-prev"><i className="fas fa-chevron-left"></i></button>
                        <button onClick={goToNextSlide} className="carousel-next"><i className="fas fa-chevron-right"></i></button>

                    </div>

                </div>
            </div>







            <div className='container w-100'>
                <Slider {...settingsproduct} ref={productSliderRef}>
                    <div className="item">
                        <div className="card">
                            <img className='w-100' src="/img/home/PRODUCT-21.png" alt="Coconut Oil" />
                            <h4 className="card-heading">Coconut Oil</h4>
                            <p className="pt-0 mb-0"> 22.4 ADE</p>
                            <p className="pt-0"><span><i className="fas fa-star px-1 "></i></span>4.9(25 Reviews)</p>
                            <button className="product-add-btn"> Add To Cart </button>
                        </div>
                    </div>
                    <div className="item">
                        <div className="card">
                            <img className='w-100' src="/img/home/PRODUCT-21.png" alt="Coconut Oil" />
                            <h4 className="card-heading">Coconut Oil</h4>
                            <p className="pt-0 mb-0"> 22.4 ADE</p>
                            <p className="pt-0"><span><i className="fas fa-star px-1 "></i></span>4.9(25 Reviews)</p>
                            <button className="product-add-btn"> Add To Cart </button>
                        </div>
                    </div>
                    <div className="item">
                        <div className="card">
                            <img className='w-100' src="/img/home/PRODUCT-21.png" alt="Coconut Oil" />
                            <h4 className="card-heading">Coconut Oil</h4>
                            <p className="pt-0 mb-0"> 22.4 ADE</p>
                            <p className="pt-0"><span><i className="fas fa-star px-1 "></i></span>4.9(25 Reviews)</p>
                            <button className="product-add-btn"> Add To Cart </button>
                        </div>
                    </div>
                    <div className="item">
                        <div className="card">
                            <img className='w-100' src="/img/home/PRODUCT-21.png" alt="Coconut Oil" />
                            <h4 className="card-heading">Coconut Oil</h4>
                            <p className="pt-0 mb-0"> 22.4 ADE</p>
                            <p className="pt-0"><span><i className="fas fa-star px-1 "></i></span>4.9(25 Reviews)</p>
                            <button className="product-add-btn"> Add To Cart </button>
                        </div>
                    </div>
                </Slider>

            </div>

        
    <div className="container-fluid p-0 pt-5 mt-5">
        <img className="w-100" src="/img/home/delevry.jpg"/>
    </div>

        </>
    );
};

export default Home;
