import React, { useContext, useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { CartContext } from '../CartContext';

const Checkoutnew = () => {
  const [name, setName] = useState("");
  const [phone, setPhone] = useState("");
  const [address, setAddress] = useState("");
  const [address2, setAddress2] = useState("");
  const [city, setCity] = useState("");
  const [state, setState] = useState("");
  const [zip, setZip] = useState("");
  const navigate = useNavigate();
  const { cart, removeFromCart, updateQuantity } = useContext(CartContext);

  useEffect(() => {
    const token = localStorage.getItem('token');
    if (!token) {
      navigate('/login');
    }
  }, [navigate]);

  const handlePlaceOrder = async (e) => {
    e.preventDefault();
  
    // Prepare the order data
    const orderData = {
      name,
      phone,
      address,
      address2,
      city,
      state,
      zip,
      cart,  // Including cart items in the order
    };
  
    // Send a POST request to backend
    try {
      const response = await fetch('http://localhost:3001/api/orders', { // Corrected backend URL
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${localStorage.getItem('token')}`,
        },
        body: JSON.stringify(orderData),
      });

      // Check if the response is OK (status 200-299)
      if (response.ok) {
        const data = await response.json();
        alert('Order placed successfully!');
        // Redirect to another page after successful order
        navigate('/order-success');
      } else {
        const errorData = await response.json();
        alert(errorData.message || 'Failed to place order');
      }
    } catch (error) {
      alert('Error placing order');
      console.error(error);
    }
  };

  return (
    <div className='container'>
      <div className='row'>
        <div className='col-lg-6 col-sm-6'>
          <h1 className='mt-5'>Checkout</h1>
          <h5>Shopping Cart</h5>
          <div>
            {cart.map(item => (
              <div key={item.id} className='d-flex mt-4'>
                <div><img className='cart-img' src={item.img} alt={item.name} /></div>
                <div className='w-100'>
                  <h6 className='d-flex mb-0'>
                    <span>{item.name}</span>
                    <span className='ml-auto'>
                      <button className='btn p-0' onClick={() => removeFromCart(item.id)}>
                        <i className="fas fa-times"></i>
                      </button>
                    </span>
                  </h6>
                  <div className='d-flex'>
                    <h6 className='mb-0'>${item.price}</h6>
                    <h6 className='mb-0 ml-auto mr-auto'>
                      <span className='pr-5'>${item.price * item.quantity}</span>
                      <button className='btn pt-0' onClick={() => updateQuantity(item.id, item.quantity - 1)} disabled={item.quantity <= 1}>-</button>
                      {item.quantity}
                      <button className='btn pt-0' onClick={() => updateQuantity(item.id, item.quantity + 1)}>+</button>
                    </h6>
                  </div>
                </div>
              </div>
            ))}
          </div>
          <h5 className='mt-5 mb-5'>Total: ${cart.reduce((total, item) => total + item.price * item.quantity, 0)}</h5>
        </div>
        <div className='col-lg-6 col-sm-6'>
          <form className='mt-5' onSubmit={handlePlaceOrder}>
            <div className="mb-3 row">
              <div className="col">
                <label htmlFor="formGridName" className="form-label">Name</label>
                <input type="text" className="form-control" placeholder="Enter name" onChange={(e) => setName(e.target.value)} />
              </div>
              <div className="col">
                <label htmlFor="formGridPhone" className="form-label">Phone No</label>
                <input type="number" className="form-control" placeholder="Phone number" onChange={(e) => setPhone(e.target.value)} />
              </div>
            </div>

            <div className="mb-3">
              <label htmlFor="formGridAddress1" className="form-label">Address</label>
              <input type="text" className="form-control" placeholder="1234 Main St" onChange={(e) => setAddress(e.target.value)} />
            </div>

            <div className="mb-3">
              <label htmlFor="formGridAddress2" className="form-label">Address 2</label>
              <input type="text" className="form-control" placeholder="Apartment, studio, or floor" onChange={(e) => setAddress2(e.target.value)} />
            </div>

            <div className="mb-3 row">
              <div className="col">
                <label htmlFor="formGridCity" className="form-label">City</label>
                <input type="text" className="form-control" onChange={(e) => setCity(e.target.value)} />
              </div>
              <div className="col">
                <label htmlFor="formGridState" className="form-label">State</label>
                <input type="text" className="form-control" onChange={(e) => setState(e.target.value)} />
              </div>
              <div className="col">
                <label htmlFor="formGridZip" className="form-label">Zip</label>
                <input type="text" className="form-control" onChange={(e) => setZip(e.target.value)} />
              </div>
            </div>

            <div className="mb-3 form-check">
              <input type="checkbox" className="form-check-input" value="cash-on-delivery" />
              <label className="form-check-label" htmlFor="formGridCheckbox">Cash On Delivery</label>
            </div>

            <button type="submit" className="btn btn-primary">Place Order</button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default Checkoutnew;
